.location-cards > a {
  text-decoration: inherit !important;
  color: #000000 !important;
}

@media (max-width: 991px) {
  .locations-sidebar {
    display: none;
  }
}

@media (min-width: 992px) {
  .mobile-nav {
    display: none;
  }
}

.locations-sidebar {
  overflow-y: auto;
}

.mobile-nav {
  position: absolute;
  z-index: 1000;
  width: 100%;
}
